  font-size: 16px;
}

@font-face {
  font-family: "Oswald";
  src: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
}
* {
  font-family: Georgia, serif;
}

* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
  font-size: 1rem;
  text-decoration: none;
  color: rgb(0, 0, 0);
  font-family: sans-serif, monospace, "Oswald";
}

body {
  background: rgb(165, 177, 179);
  background: linear-gradient(0deg, rgb(179, 179, 198) 0%, rgb(207, 217, 219) 0%, rgb(123, 205, 250) 100%);
  max-width: 100vw;
  min-height: 100vh;
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 20px;
}

#main_container {
  flex: 1;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  gap: 20px;
  border-radius: 10px;
  background-color: rgba(236, 236, 236, 0.829);
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.3);
  border: none;
  padding: 20px;
  min-width: fit-content;
}

h1 {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
  max-width: 100%;
  color: rgb(0, 0, 0);
  font-size: 3rem;
  border-radius: 10px;
  background-color: rgba(236, 236, 236, 0.829);
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.3);
  border: none;
}
h1 img {
  height: 3rem;
}

#menu {
  border-radius: 10px;
  background-color: rgba(236, 236, 236, 0.829);
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.3);
  border: none;
  max-width: 100%;
  padding: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 15px;
}
#menu * {
  font-size: 1.7rem;
}
#menu div {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 6px;
}

#search_field {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 3px;
  border-radius: 10px;
  background-color: rgb(255, 254, 254);
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.3);
  border: none;
  border-radius: 4px;
  width: 200px;
  height: auto;
}

button {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 6px;
  border-radius: 10px;
  background-color: rgb(255, 254, 254);
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.3);
  border: none;
  border-radius: 4px;
}

button:hover,
#search_field:hover {
  background-color: rgb(243, 243, 243);
}

button:hover {
  cursor: pointer;
}

#display {
  border-radius: 10px;
  background-color: rgba(236, 236, 236, 0.829);
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.3);
  border: none;
  min-width: 100%;
  max-width: 100%;
  min-height: 300px;
  height: auto;
  width: auto;
  padding: 10px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  grid-auto-rows: minmax(330px, auto);
  gap: 10px;
}

.card {
  border-radius: 10px;
  background-color: rgba(189, 189, 189, 0.212);
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.3);
  border: none;
  padding: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 5px;
}
.card * {
  font-size: 25px;
  text-align: center;
}
.card img {
  height: 120px;
  border-radius: 10px;
  background-color: rgba(236, 236, 236, 0.829);
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.3);
  border: none;
  background: linear-gradient(0deg, rgb(179, 179, 198) 0%, rgb(207, 217, 219) 0%, rgb(123, 205, 250) 100%);
}

.placeholder {
  text-align: center;
}

@media screen and (max-width: 600px) {
  :root {
    font-size: 10px;
  }
  #menu {
    flex-direction: column;
    width: 100%;
  }
  #menu div {
    width: 100%;
  }
  #menu div #search_field {
    width: 100%;
  }
  h1 {
    width: 100%;
  }
